@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header{
  background-color: #dfeefc;
  display: grid;
  height : 60px;
  /* padding-top :13px; */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  grid-template-areas: "myArea myArea";
  
}
.fixed-top-my{
  position: fixed;
  top:0;
  width: 100%;
}
.mobileList{
  text-align: center;
  grid-row-gap: 20px;
  grid-template-areas: "a";
  padding-right:32px;
  padding-bottom: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}
header.dropNav{
  grid-template-areas: "myArea";
  background-color: rgb(0,0,0,0);
  
  padding: 0%;
  box-shadow: none;
}
header.dropNav > div{
  background-color: #dfeefc;
  display: grid;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  height : 60px;
  padding-top:13px;
  display: grid;
  grid-template-areas: "a a";
}
button{
  height : -webkit-max-content;
  height : max-content;
}
svg > g{
  fill: rgb(27, 0, 179);
}
header.dropNav > div > button{
  text-align: right;
  background-color: rgb(0,0,0,0);
  border: 0;
}
header.dropNav > ul{
  background: azure ;
}
.hideThis{
  display: none;
}
#mainList{
  list-style-type: none;

}
#mainList >li> button{
  margin-top: 13px;
}
#mainList >li > a , .heading {
  text-decoration: none;
  font-size: 20px;
  padding-top: 13px;
}

.desktopList{
  text-align: center;
  display :grid;
  grid-template-areas: "a a a a a";
}
.mobileList > li, .mobileList > li > a{
  display: block;
  width:100%;
}
.mobileList  > li >a{
  height: 100%;
  padding-top:10px;
  padding-bottom: 10px;
  color: rgb(31, 16, 83);
  
}

li,a {
  display: block;
}
#mainList > li >a{
  
  height:60px;
  width: inherit;
}
.desktopList > li >a{
  
  height:60px;
  width: 150px;
}

#mainList > li > a:hover {
  /* border-left-width: 1px;
  border-right-width: 1px;
  border-top-width: 0;
  border-bottom-width: 0;
  border-color: blue;
  border-style: solid; */
  background-color: rgb(153, 211, 250);
  color :rgb(255, 255, 255);
}
a.activeLink{
  background-color:rgb(88, 178, 238);
  color:white;
}
.mobileList > li {
  padding-left : 1px;
  padding-right: 1px;
}

#dropMenu{
  text-align: left;
  background-color: rgb(93, 224, 104);
}
#dropMenu>li{
  display: block;
  padding-top:5px;
  font-size: 16px;
  margin-left: 0;
  margin-right: 0;
  height: 40px;;
}
#dropMenu > li > a{
  color:blue;
